import Joi from 'joi';

const form = {
	data() {
		return {
			loading: false,
			issues: {},
			valid: false,
			message: null
		};
	},
	mounted() {
		this.clearErrors();
		this.clearFormField();
	},
	methods: {
		/**
		 * Clear form issues 
		 */
		clearErrors(field = null) {
			if (!field) {
				this.issues = {};
			} else {
				delete this.issues[field];
			}
		},
		/**
		 * Clear all form fields
		 */
		clearFormField() {
			Object.keys(this.itemData).forEach((key) => {
				this.itemData[key] = '';
			});
		},
		/**
		 * Validate
		 */
		validate(value, schema) {
			const result = Joi.validate(value, schema, { abortEarly: false });
			if (result.error) {
				result.error.details.forEach((error) => {
					this.issues[error.path[0]] = error.message;
				});
			}
			return true;
		}
	}	
};

export default form;
